<template>
  <div v-if="smoothie" class="edit-smoothie container">
    <h2>edit {{ this.$route.params.smoothie_slug }}</h2>
    <h2>aaa</h2>
    <form @submit.prevent="EditSmoothie">
      <div class="field-title">
        <label for="title">smoothie Title:</label>
        <input type="text" name="title" v-model="smoothie.title">
      </div>
      <div v-for="(ing, index) in smoothie.ingredients" :key="index" class="field">
        <lavel for="ingredients">Ingredient:</lavel>
        <input type="text" name="ingredients" v-model="smoothie.ingredients[index]">
        <i class="material-icons delete" @click="deleteIng(ing)">delete</i>
      </div>
      <div class="field add-ingredient">
        <label for="add-ingredient">Add ingredient:</label>
        <input type="text" name="add-ingredient" @keydown.tab.prevent="addIng" v-model="another">
      </div>
      <div class="field center-align">
        <p v-if="feedback" class="red-text">{{ feedback }}</p>
        <button class="btn pink">Update Smoothie</button>
      </div>
    </form>
  </div>
</template>

<script>
import db from '@/firebase/init'
import slugify from 'slugify'

export default {
  name: 'EditSmoothie',
  data() {
    return {
      smoothie: null,
      another: null,
      feedback: null
    }
  },
  methods: {
    EditSmoothie() {
      if(this.smoothie.title) {
        this.feedback = null,
        this.slug = slugify(this.smoothie.title, {
          replacement: '-',
          remove: /[$*_~.()'"!\-:@]/g,
          lower: true
        })
        db.collection('smoothies').coc(this.smoothie.id).update({
          title: this.smoothie.title,
          ingredients: this.ingredients,
          slug: this.slug
        }).then(() => {
          this.$router.push({ name: 'Index'})
        }).catch(err =>{
          console.log(err)
        })
      } else {
        this.feedback = "You must enter a title"
      }
    },
    addIng() {
      if (this.another) {
        this.smoothie.ingredients.push(this.another)
        this.another = null
        this.feedback = null
      } else {
        this.feedback = "You must enter a value to add ingredients"
      }
    },
    deleteIng(ing) {
      this.smoothie.ingredients = this.smoothie.ingredients.filter( ingredient => {
        return ingredient != ing
      })
    }
  },
  created() {
    let ref = db.collection('smoothies').where('slug', '==', this.$route.params.smoothie_slug)
    ref.get().then(snapshot => {
      snapshot.forEach(doc => {
        let smoothie = doc.data()
        this.smoothie.id = doc.id
      })
    })
  }
}
</script>

<style scoped>
.edit-smoothie {
  margin-top: 60px;
  padding: 20px;
  max-width: 500px;
}
.edit-smoothie h2 {
  font-size: 2em;
  margin: 20px auto;
}
.edit-smoothie .field {
  margin: 20px auto;
  position: relative;
}
.edit-smoothie .delete {
  position: absolute;
  right: 0;
  bottom: 16px;
  color: #aaaaaa;
  font-size: 1.4em;
  cursor: pointer;
}
</style>



